/**Componentes */
import Card from "../sections/Card";

/**Librerías */
import {useState, useEffect} from "react";


/**Main */
const Cupcakes = ({peticion, title, home}) => {
                //peticion => saber la ruta exacta para llamar datos de API
                //title => saber si queremos colcar el título de  la página o no

    const URL = `${process.env.REACT_APP_URL_API}cupcakes`; // URL que pasaremos como parámetro para actualizar cupcake en caso sea vendido
    
    //Hook(useState() => Guardar datos traidos de la API)
    const[cupcakes, setCupcakes] = useState(); // Inicializamos el useState([]) con corchetes en vacío

    //Hook(useEffect() => Se activa la orden cuando el documento renderiza por primera vez o se actualiza según su array)
    useEffect(() => {

        //fetch => Promesas con APIS
        fetch(`${process.env.REACT_APP_URL_API}${peticion}`)
            .then(response => response.json())
            .then(data => setCupcakes(data)) //data => console.log(data): Mostrar que datos recibimos
                        //setCupcakes(data) => Guardamos los datos del JSON en el arrray de UseState()
            .catch(e => console.log(e));  // Catch siempre se coloca para saber si existe algún error en el backend
            

    }, [peticion]);
    // Se coloca petición en el array por si existe algún cambio y este debe renderizar de nuevo
    // React reconoce esto y recomienda colocarlo ahí como una dependencia

    return(

    <div className="ed-grid">
        {
            // && => Forma de escribir un if simple
            title && <h1 className="title-page">Cupcakes</h1> 
        }  

        {
            //Si cupcakes existe, imprimir tarjetas. Sino, imprimir un span "Loading" => ? Ternario
            cupcakes ? (

                <section className="ed-grid s-grid-2 m-grid-2 lg-grid-4 row-gap s-mb-4 s-pt-3 card-container">
                {   
                    /**Si está en home, se imprimen solo 4 cards */
                    home ? (
                        //map() => Recorreremos la variable del useState() donde guardamos los datos
                        //slice(0,4) => limitará los resultados del map()
                        cupcakes.slice(0, 4).map(c => (

                            <Card 
                                key = {c.id} // Key siempre se coloca como identificador único de Card cuando usamos map()
                                image = {c.image}
                                descripcion = {c.descripcion}
                                sabor = {c.sabor}
                                color = {c.color}
                                precio = {c.precio}
                                vendido = {c.vendido}
                                URL = {URL}
                                id = {c.id}

                            />
                        ))
                    ) : (

                        /**Si no está en home, se imprimen todas las tarjetas */
                        cupcakes.map(c => (

                            <Card 
                                key = {c.id} // Key siempre se coloca como identificador único de Card cuando usamos map()
                                image = {c.image}
                                descripcion = {c.descripcion}
                                sabor = {c.sabor}
                                color = {c.color}
                                precio = {c.precio}
                                vendido = {c.vendido}
                                URL = {URL}
                                id = {c.id}

                            />
                        ))
                    )
                }
                </section>

            ) : ( <span>Cargando...</span> )

        }

    </div>

    )
};

export default Cupcakes;