/**Librerías */
import { useState, useEffect } from "react";
import { get } from "axios";

/**Main Hook */
const useFetch = (peticion) => {

    const [data, setData] = useState();
    const [error, setError] = useState(); // useState() para control de errores

    useEffect(() => {

        //Llamando API con librería get de AXIOS
        get(`${process.env.REACT_APP_URL_API}${peticion}`)
            .then(({data}) => setData(data)) // Se declara ({data}) porque AXIOS devuelve un objeto data y dentro de él otra variable llamada data(Reestructuración)
            .catch(e => setError(e)); // Control de errores
            
    }, [peticion]);

    return [data, error]; // Retorna un array con [Datos de API, Error si existiera]
}

export default useFetch;

/**Notas */
//Hook creado para reutilizar gran parte del código en los componentes