import logo from "../../images/logo.PNG"

const AboutUs = () => (

    <>
    <h1 className="title-page">Sobre Nosotros</h1>
    <div className="ed-grid rows-gap s-grid-1 m-grid-2 lg-grid-2 s-mb-4 s-pt-4">
        <div className="card__data s-border s-radius-2 s-radius-bl s-pxy-2 img-company">
                <img src={logo} alt="Empresa_logo"/>
        </div>
        <div className="card__data s-border s-radius-2 s-radius-bl s-pxy-2 info-company">
            <h3 className="t5 s-mb-2 s-center">Cupcake Perú</h3>
            <div className="s-mb-2 s-main-center">
                <div className="card__teacher s-cross-center">
                <p>Somos una empresa peruana dedicada a la venta de cupcakes en todo el país. Contamos con más de 10 años en el mercado y nuestra meta a largo plazo es ser una de las empresas más reconocidas a nivel nacional como distribuidora de cupcakes.</p>
                </div>
            </div>
        </div>
    </div>
    </>

);

export default AboutUs;