/**Librerías */
import useFetch from "../../hooks/useFetch";


/**Main */
const Services = ({peticion}) => {

    /**Hook useFetch() creado por nosotros */
    const [data, error] = useFetch(peticion);

    /**Return */
    if(error){

        return(<span>{JSON.stringify(error)}</span>)

    }else{

        return(
            <div className="service">
                {
                    data ? (
                        
                        <section className="ed-grid s-grid-2 m-grid-2 lg-grid-2 row-gap s-mb-4 s-pt-3 service-box">
                        {   
    
                            data.map(c => (
    
                                <div key={c.id}>
                                    <h2>{c.nombre}</h2>
                                    <p>{c.descripcion}</p>
                                    <span>{c.contacto}</span>
                                </div>
    
                            ))
                        }
                        </section>
    
                    ) : (<span> No hay servicios </span>)
                }
    
            </div>
    
        );

    }
    

}

export default Services;