/**Librerías */
import PropTypes from "prop-types"
import { put } from "axios";
import { useState, useEffect } from "react";

/**Main */
const Card = ({image, descripcion, sabor, color, precio, vendido, URL, id}) => {

    const [estado, setEstado] = useState(vendido); // Se asigna el estado de cada card en TRUE or FALSE, para ver si está vendido o no y dependiendo de eso, se generarán cambios en la página

    useEffect(() => {

        console.log("Great");

    }, [estado]); // Cada vez que cambie el estado(Se venda o no se venda), renderizará de nuevo el card

    const vender = () => {

        setEstado(true); // Se logró vender, así que cambia el estado de tarjeta a true

        var cupcakeVendido = {
            id: id,
            sabor: sabor,
            color: color,
            descripcion: descripcion,
            precio: precio,
            image: image,
            vendido: true // Cambio interno en la bd
        }

        put(`${URL}/${id}`, cupcakeVendido); // Cada cupcake está en un apartado del JSON: http://localhost:3050/cupcakes/2
        //put => Hacer cambios en JSON
        //put(URL específica de dato a cambiar, Objeto con los datos cambiados)
        
       
    };

    return(

        <div className="s-radius-2 s-shadow-bottom background s-shadow-card-micro white-color s-pxy-2">
            <img src={image} alt={sabor}></img>
            <br />
            <br />
            <h4>{sabor}</h4>
            <p>{descripcion}</p>
            <span>Color: {color}</span>
            <br />
            <br />
            <span>Precio: {precio}</span>
            <br />
            <br />
            {
                !estado ? <button className="button dark-color btn-venta" onClick={vender}>Comprar</button> : <><hr/><span className="vendido-span">Vendido</span></>
                                                                                //Función vender()
            }
        </div>
    )
}

/** PropTypes */
Card.propTypes = {

    precio: PropTypes.number,
    image: PropTypes.string,
    descripcion: PropTypes.string.isRequired,
    sabor: PropTypes.string.isRequired,
    color: PropTypes.string.isRequired, // .isRequired => Para parámetros que son obligatorios que vengan de la bd
    vendido: PropTypes.bool.isRequired

}

/**Default Props */

Card.defaultProps = {
    image: "https://annaspasteleria.com/images/2019post/_videoCover/IMG_4255edit.jpg",
    precio: 0
}

/**Export */
export default Card