//Librería Router
import { NavLink } from "react-router-dom";
import logo from "../../images/logo.PNG"
import { useRef } from "react";


/**Main */
const Header = () => {

  const menu_r = useRef(null); // Inicializamos el useRef

  const menuDesplegable = () =>{

    const menu = menu_r.current; // Asignamos a la variable menú la opción que capturamos con el useRef
    if(menu.classList.contains('is-active')){

      menu.classList.remove('is-active');
  
    }else{
      menu.classList.add('is-active');
    }
  }

  return(
    <header className="main-header">
    {/* Burger Button */}
    <span className="icon-stack burger-button" id="burger-button" onClick={menuDesplegable}></span>
    <nav ref={menu_r} className="right-menu">
          <ul>
            <li><NavLink to="/" exact>Inicio</NavLink></li>
            <li><NavLink to="/cupcakes" exact>Cupcakes</NavLink></li>
            <li><NavLink to="/nosotros" exact>Nosotros</NavLink></li>
          </ul>
        </nav>
    {/* Main Menú */}
    <div className="ed-grid s-grid-5 lg-grid-4">
      <div className="s-cols-4 lg-cols-1 s-cross-center"><a href="/"><img className="main-logo" src={logo} alt="Logo Cupcake"/></a></div>
      <div className="s-grid-1 lg-cols-3 s-cross-center s-main-end header-links">
        <nav className="main-menu">
          <ul>
            <li><NavLink to="/" exact>Inicio</NavLink></li>
            <li><NavLink to="/cupcakes" exact>Cupcakes</NavLink></li>
            <li><NavLink to="/nosotros" exact>Nosotros</NavLink></li>
          </ul>
        </nav>
        </div>
      </div>
    </header>

  )
}

export default Header;

//Exact se agrega para decirte al router que cada uno es independiente