/**Componentes */
import Cupcakes from "./cupcakes";
import Banner from "../sections/Banner"
import Advice from "../sections/Advice";
import Declarations from "../sections/Declarations";
import Services from "../sections/Services";

/**Main */
const Home = () => (

  <>
    {/* Banner */}
    <Banner/>

    {/* Cupcakes de muestra */}
    <Cupcakes peticion="cupcakes" home/>

    {/* Aviso */}
    <Advice />

    {/* Declaraciones */}
    <Declarations/>

    {/* Servicios */}
    <Services peticion="servicios"/>

  </>
);

export default Home;